export default {
	data() {
		return {
			listData: []
		}
	},
	onLoad() {
		uni.request({
			url: this.$Url + '/api/school/coach/list',
			method: 'GET',
			data: {},
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: (res) => {
				if (res.data.code == 200) {
					var arr = []
					for (let i in res.data.msg) {
						res.data.msg[i].avatar = this.$Url + res.data.msg[i].avatar
						arr.push(res.data.msg[i]); //属性
					}
					this.listData = arr
					console.log(arr)
				} else {
					uni.showToast({
						icon: 'none',
						title: '网络不给力，请稍后重试',
						duration: 2000
					});
				}

			}
		});
	},
	methods: {
		imageError: function(e) {
			this.listData[e.currentTarget.dataset.id].avatar = '../../../static/picture/coachAvatar.png'
		}
	}
}
